import React, { useEffect, useRef, useState } from "react";
import Webcam from "react-webcam";
import { Camera } from "@mediapipe/camera_utils";
import { Hands, Results } from "@mediapipe/hands";
import { drawCanvas } from "../../../../../utils/drawCanvas";
import { useHandContext } from "../../../../../context/HandContext";

interface HandProps {
  onCloseModal: () => void;
  canvasRef: React.RefObject<HTMLCanvasElement>;
}

const SignHand: React.FC<HandProps> = ({ onCloseModal, canvasRef }) => {
  const webcamRef = useRef<Webcam>(null); //     ref 
  // const canvasRef = useRef<HTMLCanvasElement>(null); //   canvas
  const canvasRefTop = useRef<HTMLCanvasElement>(null); //     canvas
  const resultsRef = useRef<Results>(); // Results :   
  const [coordList] = useState<{ x: number; y: number }[]>([]); //   
  const { handleBaseDataUrlChange } = useHandContext();

  //  
  // Hands        ResultsListener 
  // ResultsListener       convasRef  
  const ResultsListener = (results: Results) => {
    resultsRef.current = results;

    const canvasCtx = canvasRef.current!.getContext("2d")!;
    const canvasCtxTop = canvasRefTop.current!.getContext("2d")!;
    const baseDataUrl = drawCanvas(canvasCtx, results, coordList, canvasCtxTop);

    handleBaseDataUrlChange(baseDataUrl); //     Work base64  

    if (baseDataUrl !== "") {
      onCloseModal();
      console.log("close modal");
    }
  };

  //  
  // Hands  
  // locateFile        
  useEffect(() => {
    const hands = new Hands({
      locateFile: (file) => {
        return `https://cdn.jsdelivr.net/npm/@mediapipe/hands/${file}`;
      },
    });

    //    
    hands.setOptions({
      // selfieMode : true,
      maxNumHands: 2,
      modelComplexity: 1,
      minDetectionConfidence: 0.5,
      minTrackingConfidence: 0.5,
    });

    // ResultsListener   
    //      ResultsListener   .
    hands.onResults(ResultsListener);

    // Camera      camera  
    // <video>   
    // Camera  onFrame    .    
    //   ,   Hands  

    if (
      typeof webcamRef.current !== "undefined" &&
      webcamRef.current !== null
    ) {
      const camera = new Camera(webcamRef.current.video!, {
        onFrame: async () => {
          try {
            await hands.send({ image: webcamRef.current!.video! });
          } catch (error) {
            console.log(error);
          }
        },
        width: 920,
        height: 515,
      });
      camera.start();
    }

    // Hand    canvas 
    const canvasCtx = canvasRef.current!.getContext("2d")!;
    canvasCtx.fillStyle = "#FFF";
    canvasCtx.globalAlpha = 0.0;
    canvasCtx.fillRect(0, 0, canvasCtx.canvas.width, canvasCtx.canvas.height);
  }, [ResultsListener]);

  return (
    <div>
      {/*  */}
      <div style={{ position: "relative", width: "920px", height: "515px" }}>
        <canvas
          style={{ position: "absolute", top: "0px", left: "0px" }}
          ref={canvasRef}
          width={920}
          height={515}
        />
        <canvas
          style={{ position: "absolute", top: "0px", left: "0px" }}
          ref={canvasRefTop}
          width={920}
          height={515}
        />
      </div>
      {/*   */}
      <Webcam
        audio={false}
        style={{ visibility: "hidden" }}
        mirrored={true}
        width={920}
        height={515}
        ref={webcamRef}
        screenshotFormat="image/jpeg"
        videoConstraints={{ width: 920, height: 515, facingMode: "user" }}
      />
    </div>
  );
};

export default SignHand;
